"use client"
import Input from "@/app/components/ui/Input"
import Button from "@/app/components/ui/button"
import Footer from "@/app/components/ui/footer";
import Navbar from "@/app/components/ui/navbar";
import React, {useRef} from "react"


export default function Signup() {
    const usernameRef = useRef<HTMLInputElement>(null);
    const passwordRef = useRef<HTMLInputElement>(null);
   
    return <div className="   bg-gradient-to-r from-indigo-100 via-purple-100 to-pink-100 ">
        <Navbar/>
        <div className="flex justify-center mt-20 text-4xl text-gray-800/30 drop-shadow-xl"> SignIn </div>
        <div className="flex justify-center mt-2 text-black text-lg"> Don't you have an account? Create one here</div>
       
        <div className=" flex justify-center w-full px-40 py-10  ">
        <div className="flex  flex-col justify-center space-y-5 shadow-2xs p-20 bg-white/30   "> 
          
             <Input reference={usernameRef} placeholder="email"/>
             <Input reference={passwordRef}  placeholder="password"/>
             <div className="flex justify-center"> 
             <Button width="bold" title={'Signup'} color="purple" size="sm" />
             </div>
             </div>
        </div>
         <Footer/>

    </div>
}